import { Route, Routes, HashRouter } from "react-router-dom";
import Home from "./Views/Home";
import VideoPage from "./Views/VideoPage";
import "./App.css";

function App() {
  return (
    <>
      <HashRouter>
        <Routes>
          <Route path={"/"} element={<Home />} />
          <Route path={"/video"} element={<VideoPage />} />
        </Routes>
      </HashRouter>
    </>
  );
}

export default App;
